<template>
  <div id="app">
    <img class="logo" src="./assets/logo.png">
    <br>
    <img class="logo" src="./assets/dj.png">
    <router-view/>
    <p>
      Welcome to your Django - Vue.js app {{ fullName }}!
    </p>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  name: 'app',
  computed: {
    ...mapGetters({
      fullName: 'fullName'
    })
  }
}
</script>

<style lang="sass">
html
  height: 100%

body
  display: flex
  align-items: center
  justify-content: center
  height: 100%

#app
  color: #2c3e50
  margin-top: -100px
  max-width: 600px
  font-family: Source Sans Pro, Helvetica, Arial, sans-serif
  text-align: center


#app a
  color: #42b983
  text-decoration: none


.logo
  width: 100px
  height: 100px
</style>



