export default {
  namespaced: true,
  state: {
    name: 'Bojack',
    age: 34,
    level: 4
  },
  getters: {
    userAnnouncement: (state, getters, rootState) => phrase => {
      return state.name.toUpperCase() + (phrase ? ' says ' + phrase : '')
    }
  },
  mutations: {
    adjustLevel (state, payload) {
      state.level += payload.amount
    }
  }
}



