import Vuex from 'vuex'
import Vue from 'vue'
import axios from 'axios'

import user from './modules/user'
import email from './modules/email'

Vue.use(Vuex)

function initialState () {
  return {
    tokenKey: '',
    profile: {
      id: '-1',
      firstName: '',
      lastName: ''
    }
  }
}

const state = initialState()

export const store = new Vuex.Store({
  strict: true, // TODO: DISABLE FOR PRODUCTION
  modules: {
    user,
    email
  },
  state,
  getters: {
    fullName (state, getters) {
      return state.profile.firstName + ' ' + state.profile.lastName
    }
  },
  mutations: {
    clearData (state, payload) {
      const iS = initialState()
      for (let item in state) {
        Vue.set(state, item, iS[item])
      }
    },
    setKey (state, payload) {
      state.tokenKey = payload.key
    },
    setName (state, payload) {
      state.profile.firstName = payload.first_name
      state.profile.lastName = payload.last_name
    }
  },
  actions: {
    async login ({commit, dispatch}, payload) {
      console.log(payload)
      console.log('logging in...')
      let {data} = await axios.post('rest-auth/login/', {
        username: payload.username,
        password: payload.password
      })
      console.log(data)
      commit('setKey', data)
      dispatch('getName')
    },
    async logout ({commit}, payload) {
      const {data} = await axios.post('rest-auth/logout/')
      console.log(data)
      commit('clearData')
    },
    async getName ({commit}) {
      const {data} = await axios.get('api-test/')
      console.log('in actions', data)
      commit('setName', data)
    }
  }
})



